






import { Component, Vue } from 'vue-property-decorator';
import { mapGetters } from 'vuex';
import SmsTable from './sms-table.vue';

@Component({
    components: { SmsTable },
    computed: {
        ...mapGetters({
            sms: 'sms/SMS',
            loading: 'sms/LOADING'
        })
    }
})
export default class SmsList extends Vue {
    mounted() {
        this.$store.dispatch('sms/LIST');
    }
}
